<template>
  <div class="controlPanel" ref="control">
    <div class="title" ref="controls">
      控制面板
    </div>
    <div class="gemoetry">
      <div class="tit">
        几何图形
      </div>
      <div class="gemo">
        <div class="g" @click="addgemo(0)">
          <img src="../../assets/gemo.png">
        </div>
        <div class="g" @click="addgemo(1)">
          <img src="../../assets/gemo1.png">
        </div>
        <div class="g" @click="addgemo(2)">
          <img src="../../assets/gemo2.png">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import {$canvas} from '@/utils/canvasInit'
export default {
  name: 'controlPanel',
  components: {
    // HelloWorld
  },
  data(){
    return {
      canvas:null
    }
  },
  props: {
	  setting:{
	    type: Object,
	    default:null
	  }
  },
  computed: {
    // 把 `this.name` 映射为 `this.$store.state.name`
    // ...mapState({
    //   $canvas: state => state.canvas
    // }),
    // 把 `this.getName` 映射为 `this.$store.getters.getName`
    ...mapState('cans',{
      $gemo: state => state.gemo
    }),
    ...mapState('three',{
      $group: state => state.group
    }),
    ...mapGetters([
      // 把 `this.getName` 映射为 `this.$store.getters.getName`
      "getCanvas"
    ])
  },
  created() {
  },
  watch: {
  },
  mounted(){
  	this.$nextTick(()=>{

      let max_left = $(this.$refs.control).parent().outerWidth() - $(this.$refs.control).outerWidth(),
          max_top = $(this.$refs.control).parent().outerHeight() - $(this.$refs.control).outerHeight();
      $(this.$refs.controls).on('mousedown', event => {
          let ele_x = event.offsetX,
              ele_y = event.offsetY;
          $(document).on('mousemove', e => {
              e.preventDefault();
              let left = e.clientX - ele_x,
                  top = e.clientY - ele_y;
              left = left < 0 ? 0 : left;
              top = top < 0 ? 0 : top;
              left = left > max_left ? max_left : left;
              top = top > max_top ? max_top : top;
              $(this.$refs.control).css({
                  left, top
              })
          })
      })
      $(document).on('mouseup', () => {
          $(document).off('mousemove');
      })
  	})
  },
  methods: {
    ...mapMutations('cans',[
      // 将 `this.mutationSetName()` 映射为 `this.$store.commit('mutationSetName')`
      'pushGemo'
    ]),
    addgemo(pms){
      // 添加模型
      switch(pms) {
        case 0:
          // 圆形
          var gemo = new fabric.Circle({
            radius: 50,
            left:300,//距离画布左侧的距离，单位是像素
            top:300,//距离画布上边的距离
            fill:'blue',//填充的颜色
          });
          // this.$canvas.add(gemo) //全局状态调用
          // console.log($canvas)
          $canvas.add(gemo) //全局变量调用
          this.pushGemo({gemo:gemo})
          this.outImg()
          break;
        case 1:
          // 正方形
          // console.log($canvas)
          // return
          var gemo = new fabric.Rect({
            left:300,//距离画布左侧的距离，单位是像素
            top:300,//距离画布上边的距离
            fill:'black',//填充的颜色
            width:100,//方形的宽度
            height:100//方形的高度
          });
          $canvas.add(gemo);
          this.pushGemo({gemo:gemo})
          this.outImg()
          break;
        case 2:
          // 三角形
          var gemo = new fabric.Triangle({
            left:300,//距离画布左侧的距离，单位是像素
            top:300,//距离画布上边的距离
            fill:'red',//填充的颜色
            width:100,//方形的宽度
            height:100,//方形的高度
            angle:0
          });
          $canvas.add(gemo);
          this.pushGemo({gemo:gemo})
          this.outImg()
          break;
      }
    },
    outImg(){
      // 输出图片
      var _this =this;
      var c = $canvas.toJSON();
      $canvas.remove($canvas.getObjects()[0])
      const link = $canvas.toDataURL({
        width: 700,
        height: 700,
        left: 0,
        top: 0,
        format: 'jpg',
      })
      $canvas.loadFromJSON(c);
      // $canvas.getObjects()[0].canvas._objects[0].set('selectable',false)
      // console.log(link)
      // console.log(this.$group.children[0])
      // console.log(this.$group.children[0])
      if(this.$group){
        let image = new Image()
        image.src = link
        // image.src = require('/static/yuanzhutil.jpg')
        image.onload = function () {
          _this.$group.children[0].material.map.image = image;
          _this.$group.children[0].material.map.needsUpdate=true;
        }
      }
    }
  }
}
</script>
<style lang="less">
.controlPanel {
   width: 200px;min-height: 50px;position: fixed;top:0px;left:0px;z-index: 999;
   .title {
      width: 100%;height: 30px;line-height: 30px;background:#000;color:#fff;text-align: center;font-size: 14px;border-bottom:1px solid #fff;cursor:move;
   }
   .gemoetry {
      width: 100%;min-height: 50px;background:rgba(0,0,0,0.6);cursor: pointer;
      .tit {
        color:#ccc;background:#000;height: 25px;line-height: 25px;padding-left:10px;
      }
      .gemo {
        zoom:1;
        &:after {
          display: block;content: '';clear:both;
        }
        .g {
          float:left;width: 100px;height: 80px; display: flex;justify-content: center;align-items: center;
        }
      }
   }
}
</style>

