const cans = {
  namespaced: true,
  state: {
    test:1,
    gemo:[]
  },
  getters:{
    getGemo(state){
      return state.gemo
    },
    getTest(state){
      return state.test
    }
  },
  mutations: {
    /**设置画布对象 */
    pushGemo(state,{gemo}={}){
        state.gemo.push(gemo)
    },
  },
  actions: {
  },
  modules: {
  }
}
const three = {
  namespaced: true,
  state: {
    test:1,
    group:null
  },
  getters:{
    getGroup(state){
      return state.group
    },
    getTest(state){
      return state.test
    }
  },
  mutations: {
    /**设置画布对象 */
    setGroup(state,{group}={}){
        state.group=group
    },
  },
  actions: {
  },
  modules: {
  }
}
export {
 cans,
 three
}