  components: {
    // HelloWorld
  },
  data(){
    return {
    }
  },
  props: {
  	setting: {
	    type: Object,
	    default:null
  	}
  },
  computed: {
  },
  created() {
  },
  watch: {
  },
  mounted(){
  	this.$nextTick(()=>{
  	})
  },
  methods: {
    choices(v){
      // 切换
      switch(v) {
        case 0:
            this.$router.push({name:'Home'})
          break;
        case 1:
            this.$router.push({name:'Three'})
          break;
      }
    }
  }
}
</script>
