function _can_init({resize=false,id="canvas",init,target,resizefn}){
	/**初始化画布大小
	   @*resize        【boolean:是否自适应窗口大小】【default:false】
	   @*id        【String:canvas画布id】【default:"canvas"】
	   @*target      【目标对象】
	   @*init      【初始化回调】
	   @*resizefn      【自适应回调】
	**/

	// 初始化画布大小
	var canvas = document.getElementById(id);
	canvas.width = window.innerWidth;
	canvas.height = window.innerHeight;

	//初始化画布回调
	if(init)init(target);


	// 监听窗口大小改变画布大小保持全屏
	if(resize){
		window.addEventListener('resize',resize,false);
	};
	function resize(){
		// console.log(window.innerWidth)
		canvas.width = window.innerWidth
		canvas.height = window.innerHeight

		//初始化画布回调
		if(resizefn)resizefn(target)
	};
}


function zoomToFitCanvas({canvas}) {
	/**缩放移动视图，使其适应Canvas大小
	   @*canvas        【boolean:是否自适应窗口大小】【default:false】
	**/

	//遍历所有对对象，获取最小坐标，最大坐标
	var objects = canvas.getObjects();
	console.log(objects)
	if(objects.length > 0 ){
	  var rect = objects[0].getBoundingRect();
	  var minX = rect.left;
	  var minY = rect.top;
	  var maxX = rect.left + rect.width;
	  var maxY = rect.top + rect.height;
	  for(var i = 1; i<objects.length; i++){
	    rect = objects[i].getBoundingRect();
	    minX = Math.min(minX, rect.left);
	    minY= Math.min(minY, rect.top);
	    maxX = Math.max(maxX, rect.left + rect.width);
	    maxY= Math.max(maxY, rect.top + rect.height);
	  }
	}

	//计算平移坐标
	var panX = (maxX - minX - canvas.width)/2 + minX;
	var panY = (maxY - minY - canvas.height)/2 + minY;
	//开始平移
	canvas.absolutePan({x:panX, y:panY});

	//计算缩放比例
	var zoom = Math.min(canvas.width/(maxX - minX), canvas.height/(maxY - minY));
	//计算缩放中心
	var zoomPoint = new fabric.Point(canvas.width / 2 , canvas.height / 2);
	//开始缩放
	canvas.zoomToPoint(zoomPoint, zoom);
}

var $canvas = null
function can_init({id="canvas",init,target,resize,resizePass,width,height}={}){
	/**初始化画布大小
	   @*resize        【函数:监听窗口大小回调,停止时回调一次】
	   @*resizePass        【函数:监听窗口大小过程回调，重复回调直到停止】
	   @*id        【String:canvas画布id】【default:"canvas"】
	   @*target      【目标对象】
	   @*init      【初始化回调】
	   @@参数{a=1}={}这种写法调用的时候不用传值代表默认传空对象，可以直接使用设置的默认参数
	   @@参数{a=1}这种写法调用的时候必须传参数对象，传空值才能使用默认值，可以直接使用设置的默认参数
	**/
	const t = 0;//定时器
	const canvas = new fabric.Canvas(id,{
	    width:width || window.innerWidth,
	    height:height || window.innerHeight,
	    preserveObjectStacking:true // 禁止选中图层时自定置于顶
	})
	// canvas.renderOnAddition = true;
	$canvas  = canvas
	if(init)init({canvas:canvas,tis:target})

	// 监听窗口大小改变画布大小保持全屏
	if(resize){
		window.addEventListener('resize',resizefn,false);
	};

	function resizefn(){
		// 监听窗口大小重置画布
		if(resizePass)resizePass({tis:target})
		clearTimeout(t);
		setTimeout(()=>{
			const canvas =new fabric.Canvas('canvas',{
			    width:width || window.innerWidth,
			    height:height || window.innerHeight
			})
			$canvas  = canvas
			if(resize)resize({canvas:canvas,tis:target})
		},200)
	};
}
export  {
  can_init,
  $canvas
}