import { createStore } from 'vuex'
import {cans,three} from './storeGroup'

export default createStore({
  state: {
  	canvas:null
  },
  getters:{
    getCanvas(state){
	    return state.canvas
	}
  },
  mutations: {
  	/**设置画布对象 */
    initFabric(state,{canvas}={}){
        state.canvas=canvas
    },
  },
  actions: {
  },
  modules: {
    cans,
    three
  }
})
